<template>
  <div class="container">
    <form @submit.prevent="handlefindPokemon" class="form-pokemon">
      <div class="input-form">
        <label for="pokemon-search">Busca tu Pokemon</label>
        <input
          id="pokemon-search"
          type="text"
          required
          v-model="pokemonSearch"
        />
      </div>
      <input class="button" type="submit" value="Buscar" />
    </form>
    <section class="pokemon-contenet">
      <div class="card-pokemon">
        <div class="header-background"></div>
        <img
          v-if="pokemon.img"
          class="img-pokemon"
          :src="pokemon.img"
          :alt="pokemon.name"
        />
        <img
          class="img-pokemon"
          v-else
          src="../assets/pokebola.png"
          alt="Pokebola"
        />
        <p class="name-pokemon">{{ pokemon.name | capitalize }}</p>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pokemonSearch: "",
      pokemon: {
        img:
          "https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/dream-world/25.svg",
        name: "pikachu",
      },
    };
  },
  methods: {
    async handlefindPokemon() {
      try {
        if (this.pokemonSearch.length) {
          this.pokemon.name = "Buscando pokemon...";
          const data = await this.fetchPokemon(
            this.pokemonSearch.toLowerCase()
          );

          setTimeout(() => {
            this.pokemon.img = data.sprites.other.dream_world.front_default;
            this.pokemon.name = data.species.name;
            this.pokemonSearch = "";
          }, 1000);
        }
      } catch (error) {
        this.pokemon.name = "No existe este pokemon";
        this.pokemon.img = null;
        console.log("no encontrado");
      }
    },

    async fetchPokemon(name) {
      const res = await fetch(`https://pokeapi.co/api/v2/pokemon/${name}`);
      return res.json();
    },
  },
  filters: {
    capitalize(value) {
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
  },
};
</script>

